'use client'
import { useState } from 'react'

type Item = { q: string; a: string }

export default function FAQ({ items }: { items: Item[] }) {
  const [open, setOpen] = useState<number | null>(0)
  return (
    <div className="space-y-3">
      {items.map((it, idx) => (
        <div
          key={it.q}
          className={`rounded-2xl border transition-all duration-500 ${
            open === idx
              ? 'border-slate-300  bg-white  shadow-lg'
              : 'border-slate-200/50  bg-white/50  hover:border-slate-300  hover:bg-white '
          }`}
        >
          <button
            type="button"
            onClick={() => setOpen(open === idx ? null : idx)}
            aria-expanded={open === idx}
            aria-controls={`faq-answer-${idx}`}
            className="w-full text-left flex items-start justify-between gap-4 p-6 lg:p-7 group"
          >
            <span
              id={`faq-question-${idx}`}
              className="font-medium text-base lg:text-lg tracking-tight text-slate-900  transition-colors pr-4"
            >
              {it.q}
            </span>
            <span className="flex-shrink-0 mt-0.5">
              <svg
                className={`w-5 h-5 text-slate-400  transition-all duration-500 ${
                  open === idx ? 'rotate-180 text-slate-700 ' : 'group-hover:text-slate-600 '
                }`}
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <title>Rozbalit</title>
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
              </svg>
            </span>
          </button>
          <section
            id={`faq-answer-${idx}`}
            aria-labelledby={`faq-question-${idx}`}
            className={`overflow-hidden transition-all duration-500 ease-in-out ${
              open === idx ? 'max-h-[500px] opacity-100' : 'max-h-0 opacity-0'
            }`}
          >
            <div className="px-6 lg:px-7 pb-6 lg:pb-7">
              <p className="text-sm lg:text-base leading-relaxed text-slate-600  pt-2">{it.a}</p>
            </div>
          </section>
        </div>
      ))}
    </div>
  )
}
